import axios from '../common/js/axios'

/***
 * 文件上传
 * @param file
 * @return {AxiosPromise<any>}
 */
export function upload(file) {
    const url = `/sys/file/upload`

    return axios.post(url, {file})
}

/***
 * 文件上传
 * @param base64
 * @return {AxiosPromise<any>}
 */
export function uploadBase64(base64) {
    const url = `/sys/file/uploadBase64`

    return axios.post(url, {base64})
}


/***
 * 获取离线地图下载列表
 * @return {AxiosPromise<any>}
 */
export function downloadMapList(params) {
    const url = `/sys/download/map/getlist`

    return axios.get(url, {params})
}

/***
 * 获取离线地图文件下载连接
 * @param id
 * @return {AxiosPromise<any>}
 */
export function downloadMapUrl(id) {
    const url = `/sys/download/map/geturl`

    return axios.get(url, {params: {id}})
}

/***
 * 下载地图文件
 * @param url   // 下载地址
 * @param update   // 更新回调
 * @return {AxiosPromise<any>}
 */
export function downloadMap(url, update) {
    return axios.get(url, {}, {
        onDownloadProgress(event) {
            typeof update === 'function' && update(event)
        },
        responseType: 'blob'
    })
}

/**
 * 检查更新
 * @param version
 */
export function checkUpdate(version) {
    const url = `/sys/download/sys/checkupdate`

    return axios.get(url, {params: {version}})
}

/***
 * 获取配置
 * @param params
 * @param params.key
 * @return {*}
 */
export function getSetters(params) {
    const url = `/sys/settings`

    return axios.get(url, {params}).then(res => res.map(item => {
        return {
            id: item.id,
            typeId: item.keyWord,
            value: typeof item.value === 'string' ? item.value.split(';')[0] : item.value,
            color: typeof item.value === 'string' ? item.value.split(';')[1] || item.color : item.color
        }
    }))
}



