import {getNovice, setNovice, resetNovice} from "../../storage/novice"

const SET_NOVICE = 'SET_NOVICE'

export default {
    namespaced: true,
    state: {
        novice: getNovice()
    },
    getters: {
        novice: state => state.novice
    },
    mutations: {
        [SET_NOVICE](state, value) {
            state.novice = value
        }
    },
    actions: {
        nextStep({commit, state}) {
            const step = state.novice + 1

            commit(SET_NOVICE, setNovice(step))
        },
        resetNovice({commit}) {
            commit(SET_NOVICE, resetNovice())
        }
    }
}



