import Store from '../store'

// 网络
export const network = {
    toggle(status) {
        Store.commit('app/SET_NETWORK', status)
    }
}

// GPS
export const gps = {
    toggle(status) {
        Store.commit('app/SET_GPS', status)
    }
}

// 语音
export const speech = {}

// 文字
export const text = {}



