import Router from "../router/index"
import Store from "../store/index"
import {getDepth, getMapBoat, getMapLabel, getMapMark, getMapWater, getMapWeather} from "../api/map"
import {isArray} from "./index"

// 获取图层点列表
// 每次移动都会向后端获取数据，并将数据传递给 Android Arcgis map 处理
function getMapElementList(index) {
    switch (index) {
        case 0:
            return getMapWater
        case 1:
            return getMapBoat
        // case 2:
        //     return getMapMark
        case 3:
            return getMapWeather
        case 5:
            return getMapLabel
        default:
            return function () {
                console.error('没有输入正确类型', index)

                return Promise.resolve({})
            }
    }
}

// 图层点面板
export const element = {}

// 指南针
export const compass = {}

// 导航
export const navigation = {}

// 选点
export const selection = {}

/***
 * 地图移动
 * x 经度  y 纬度
 * @param minX
 * @param minY
 * @param maxX
 * @param maxY
 */
export function handlerMapMove(minX, minY, maxX, maxY, flag) {
    const layer = Store.getters['layer/layers']

    const data = {
        min_longitude: minX,
        max_longitude: maxX,
        min_latitude: minY,
        max_latitude: maxY
    }

    isArray(layer).forEach((item, index) => {
        if (!item.status || item.id === '013007' || item.id === '013006') return

        if (!Store.getters['user/token'] && item.id === '001001') return

        getMapElementList(index)(data).then(res => {
            if (!res || !res.length) {
                return console.log('图层渲染参数为空', 'Type Id: ', item.id)
            }

            window.Arcgis && window.Arcgis.listInfos(res, res => {
                console.log('%c地图图层点渲染成功. ', 'color: #2196f3')
            }, err => {
                console.log(err, '图层渲染失败. ')
            })
        }).catch(err => {
            console.log(err, index, '获取地图图层点数据出现错误. ')
        })
    })

    if (Store.getters['map/depth']) {
        const params = {
            dpv: window.$depth,
            minx: minX,
            miny: minY,
            maxx: maxX,
            maxy: maxY
        }

        if (flag) {
            getDepth(params).then(res => {
                window.Arcgis && window.Arcgis.setAirworthiness([res])
            })
        }
    }
}

// 搜索船舶
export const search = {
    boat(minX, minY, maxX, maxY) {
        const data = {
            min_longitude: minX,
            max_longitude: maxX,
            min_latitude: minY,
            max_latitude: maxY
        }
        getMapBoat(data).then(list => {
            window.Arcgis && window.Arcgis.listInfos(list, res => {
                console.log('%c地图图层点渲染成功. ', 'color: #2196f3')
            }, err => {
                console.log(err, '图层渲染失败. ')
            })
        })
    }
}

/***
 * 跳转至离线地图页面
 */
export function routerOfflineMap() {
    Router.push({name: 'offline-map'})
}



