import Storage from 'good-storage'

const NOVICE_KEY = '__GDHDT_NOVICE_KEY__'

export function getNovice() {
    return Storage.get(NOVICE_KEY, 0)
}

export function setNovice(value) {
    Storage.set(NOVICE_KEY, value)

    return value
}

export function resetNovice() {
    Storage.set(NOVICE_KEY, 0)

    return 0
}



